                              style="width: calc(100% - 56px)"
                              >Online payments</span
                            >
                            <base-switch
                              class="mr-1 tk-layout-inline-block"
                              v-model="setting.onlinePayments"
                              on-text="On"
                              off-text="Off"
                              type="main"
                            ></base-switch>
                          </div>
                        </div>
                      </div>
                    </span>
                  </div>

                  <!-- payment due -->
                  <div>
                    <base-input label="Payment Due">
                      <el-select v-model="setting.paymentsDueSelects.simple" filterable>
                        <el-option
                          v-for="option in setting.paymentsDueSelects.options"
                          :key="option.label"
                          :label="option.label"
                          :value="option.value"
                        >
                        </el-option>
                      </el-select>
                    </base-input>
                  </div>

                  <!-- sales tax -->
                  <div>
                    <base-input label="Sales Tax">
                      <el-select
                        v-model="setting.salesTax.simple"
                        filterable
                        placeholder="Payment Due"
                      >
                        <el-option
                          v-for="option in setting.salesTax.options"
