                      </el-select>
                    </base-input>
                  </div>

                  <!-- late fee -->
                  <div>
                    <base-input label="Late Fee" v-model="setting.lateFee.simple"></base-input>
                  </div>

                  <!-- invoice notes -->
                  <base-input label="Invoice Notes">
                    <textarea
