            >
              <div class="row align-items-center">
                <div class="col-8">
                  <h5 class="h3 mb-0">Payouts Settings</h5>
                </div>
                <div class="col-4 text-right">
                  <i class="fas fa-angle-down" v-if="!showPayoutSetting"></i>
                  <i class="fas fa-angle-up" v-if="showPayoutSetting"></i>
                </div>
              </div>
            </div>

            <slide-y-up-transition>
              <div class="card-body" v-if="showPayoutSetting">
                <form>
                  <!-- payout schedule -->
                  <div>
                    <base-input label="Payout Schedule">
                      <el-select
                        v-model="setting.payoutScheduleSelects.simple"
                        filterable
                      >
                        <el-option
                          v-for="option in setting.payoutScheduleSelects.options"
                          :key="option.label"
                          :label="option.label"
                          :value="option.value"
                        >
                        </el-option>
                      </el-select>
                      <div
                        class="invalid-feedback tk-font-color-gray"
                        style="display: block"
                      >
                        Funds will be deducted from students' payments and split
                        based on teacher payout fee.
                      </div>
                    </base-input>
                  </div>

                  <!-- repeat -->
                  <div v-if="setting.payoutScheduleSelects.simple !== 'Daily'">
                    <base-input
                      :label="setting.payoutScheduleSelects.simple"
                      v-if="setting.payoutScheduleSelects.simple === 'Weekly'"
                    >
                      <el-select
                        v-model="setting.payoutScheduleWeeklySelects.simple"
                        filterable
                      >
                        <el-option
                          v-for="option in setting.payoutScheduleWeeklySelects.options"
                          :key="option.label"
                          :label="option.label"
                          :value="option.value"
                        >
                        </el-option>
                      </el-select>
                    </base-input>
                    <base-input
                      :label="setting.payoutScheduleSelects.simple"
                      v-if="setting.payoutScheduleSelects.simple === 'Monthly'"
                    >
                      <flat-picker
                        slot-scope="{ focus, blur }"
                        @on-open="focus"
                        @on-close="blur"
                        :config="{ allowInput: true }"
                        class="form-control datepicker"
                        v-model="setting.dates.simple"
                      >
                      </flat-picker>
                    </base-input>
                  </div>

                  <!-- receipt notes -->
                  <base-input label="Receipt Notes">
                    <textarea
                      class="form-control tk-font-color-black1"
                      id="receiptNotes"
                      rows="1"
                      resize="none"
                      v-model="setting.receiptNotes"
                    ></textarea>
                  </base-input>

                  <!-- email text -->
                  <base-input label="Email Text">
                    <textarea
                      class="form-control tk-font-color-black1"
                      id="emailTextForPayouts"
                      rows="6"
                      v-model="setting.receiptEmailText"
                    ></textarea>
                  </base-input>
