                showPaymentMethodSetting = !showPaymentMethodSetting
              "
            >
              <div class="row align-items-center">
                <div class="col-8">
                  <h5 class="h3 mb-0">Payment Methods</h5>
                </div>
                <div class="col-4 text-right">
                  <i
                    class="fas fa-angle-down"
                    v-if="!showPaymentMethodSetting"
                  ></i>
                  <i
                    class="fas fa-angle-up"
                    v-if="showPaymentMethodSetting"
                  ></i>
                </div>
              </div>
            </div>

            <slide-y-up-transition>
              <div class="card-body" v-if="showPaymentMethodSetting">
                <div
                  class="card tk-font-color-main text-center tk-cursor-pointer tk-px-padding-px10 tk-border-color-gray-200"
                  @click.prevent="showCreate = true"
                >
                  <i class="fas fa-plus"></i>
                </div>
                <div v-for="(item, index) in payment" :key="index">
                  <template v-if="item.type === 2">
                    <paypal-card
                      @deletePaymentMethod="showDelete = true"
                    ></paypal-card>
                  </template>
                  <template v-else>
                    <visa-card
                      :type="item.type"
                      @deletePaymentMethod="showDelete = true"
                    ></visa-card>
