    </div>

    <modal :show.sync="showCreate">
      <template slot="header">
        <h5 class="modal-title" id="exampleModalLabel">Add Payment Method</h5>
      </template>
      <div class="tk-popup-content">
        <credit-card></credit-card>
      </div>
      <template slot="footer">
        <base-button
          type="secondary"
          @click.prevent="showCreate = false"
          class="tk-font-color-lightGray tk-border-none"
          >Close</base-button
        >
        <base-button
          type="primary"
          @click.prevent="showCreate = false"
          class="tk-font-color-white tk-bg-color-main tk-border-none"
          >Add</base-button
        >
      </template>
    </modal>

    <modal :show.sync="showDelete">
      <template slot="header">
        <h5 class="modal-title" id="exampleModalLabel">
          Delete Payment Method
        </h5>
      </template>
      <div class="tk-popup-content">Do you want to delete the payment?</div>
      <template slot="footer">
        <base-button
          type="secondary"
          @click.prevent="showDelete = false"
          class="tk-font-color-lightGray tk-border-none"
          >Close</base-button
        >
        <base-button
          type="primary"
          @click.prevent="showDelete = false"
          class="tk-font-color-white tk-bg-color-red tk-border-none"
          >Delete</base-button
        >
      </template>
    </modal>
