</template>

<script>
import $ from "jquery";
import bus from "@/bus";
import {
  Table,
  TableColumn,
  DropdownMenu,
  DropdownItem,
  Dropdown,
  Tooltip,
  Select,
  Option,
} from "element-ui";
import "flatpickr/dist/flatpickr.css";
import invoices from "../Invoices/invoices";
import transactions from "../Transactions/transactions";
import { SlideYUpTransition } from "vue2-transitions";
import payment from "../PaymentMethod/paymentMethod";

export default {
  name: "inline-actions-table",
  components: {
    [Tooltip.name]: Tooltip,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn,
    [Dropdown.name]: Dropdown,
    [DropdownItem.name]: DropdownItem,
    [DropdownMenu.name]: DropdownMenu,
    ButtonRadioGroup: () => import("@/components/ButtonRadioGroup.vue"),
    flatPicker: () => import("vue-flatpickr-component"),
    [Select.name]: Select,
    [Option.name]: Option,
    SlideYUpTransition,
    PaypalCard: () => import("../PaymentMethod/PaypalCard"),
    CreditCard: () => import("../PaymentMethod/CreditCard"),
    VisaCard: () => import("../PaymentMethod/VisaCard"),
  },
  data() {
    return {
      showCreate: false,
      showDelete: false,
      showInvoiceSetting: false,
      showPayoutSetting: false,
      showPaymentMethodSetting: false,
      invoices,
      transactions,
      payment,
      index: -1,
      currentGroup: "invoices",
      teacherListOptions: [
        { value: "invoices", label: "Invoices" },
        { value: "transactions", label: "Transactions" },
        { value: "settings", label: "Settings" },
      ],

      // setting
      setting: {
        invoiceNotes: "",
        invoiceEmailText: "",
        receiptNotes: "",
        receiptEmailText: "",
        payoutScheduleWeeklySelects: {
          simple: "Monday",
          options: [
            {
              label: "Sunday",
              value: "0",
            },
            {
              label: "Monday",
              value: "1",
            },
            {
              label: "Tuesday",
              value: "2",
            },
            {
              label: "Wednesday",
              value: "3",
            },
            {
              label: "Thursday",
              value: "4",
            },
            {
              label: "Friday",
              value: "5",
            },
            {
              label: "Saturday",
              value: "6",
            },
          ],
        },
        payoutScheduleSelects: {
          simple: "Daily",
          options: [
            {
              label: "Daily",
              value: "Daily",
            },
            {
              label: "Weekly",
              value: "Weekly",
            },
            {
              label: "Monthly",
              value: "Monthly",
            },
          ],
        },
        lateFee: {
          simple: "10.00",
          options: [
            {
              label: "10.00",
              value: "10",
            },
            {
              label: "16.00",
              value: "16",
            },
          ],
        },
        salesTax: {
          simple: "0.06",
          options: [
            {
              label: "6.00%",
              value: "0.06",
            },
            {
              label: "10.00%",
              value: "0.1",
            },
          ],
        },
        paymentsDueSelects: {
          simple: "Upon Receipt",
          options: [
            {
              label: "Upon Receipt",
              value: "Upon Receipt",
            },
          ],
        },
        dates: {
          simple: new Date().getFullYear() + "-" + (new Date().getMonth() + 1) + "-" + new Date().getDate(),
        },
        onlinePayments: true,
      },
    };
  },
  watch: {
    setting: {
      deep: true,
      handler() {
        this.previewSetting()
      }
    },
    currentGroup(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.$emit("hidePreview");
        if (newVal === "settings") {
          this.previewSetting();
        }
      }
    },
  },
  mounted() {
    let self = this;

    let invoiceList = document.getElementById("invoiceList");
    let invoice_child = invoiceList.getElementsByClassName("el-table__row");

    let transactionList = document.getElementById("transactionList");
    let transaction_child = transactionList.getElementsByClassName(
      "el-table__row"
    );

    for (var i = 0; i < invoice_child.length; i++) {
      let index = i;
      invoice_child[i].onclick = () => {
        $('.tk-selected').removeClass('tk-selected')
        invoice_child[index].classList.add('tk-selected')
        self.preview(self.invoices[index], self.currentGroup)
      };
    }

    for (var i = 0; i < transaction_child.length; i++) {
      let index = i;
      transaction_child[i].onclick = () => {
        $('.tk-selected').removeClass('tk-selected')
        transaction_child[index].classList.add('tk-selected')
        self.preview(self.transactions[index], self.currentGroup);
      };
    }
  },
  methods: {
    previewSetting() {
      let self = this;
      self.$emit("preview", {
        data: {
          salesTax: (parseFloat(self.setting.salesTax.simple) * 100).toFixed(2) + "%",
          lateFee: self.setting.lateFee.simple,
          notes: self.setting.invoiceNotes,
          emailText: self.setting.invoiceEmailText,
          receiptNotes: self.setting.receiptNotes,
          receiptEmailText: self.setting.receiptEmailText,
          schedule:
            self.setting.payoutScheduleSelects.simple !== "Monthly"
              ? self.setting.payoutScheduleSelects.simple
              : self.setting.dates.simple,
          onlinePayments: self.setting.onlinePayments
        },
        type: self.currentGroup,
      });
    },
    initSettingData() {
      let self = this;
    },
    preview(data, type) {
      this.$emit("preview", {
        data: data,
        type: type,
      });
    },
    validationEmail(obj) {
      let self = this;
      if (self.$tools.isNull(obj.email)) {
        obj.isValid = true;
      } else {
        obj.isValid = self.$tools.isEmail(obj.email);
      }
    },
  },
};
</script>
<style scoped>
