  font-size: 30px;
  margin: 0 4px;
  margin-left: -5px;
  transform: scale(0.72);
}

.status-icon-stack {
  font-size: 16px;
  transform: scale(0.72);
  /* top: -2px; */
  left: -9px;
}
</style>

